<template>
  <div class="container-pubish">
    <el-dialog title :visible.sync="centerDialogVisible" width="80%" center @close=" select=''">
      <el-tabs  v-model="selectRadio" type="card" @tab-click="handleClick" >
        <el-tab-pane label="素材库" name="imgs">
          <div>
            <el-radio-group v-model="radio_all" @change="getToggle" size="medium">
              <el-radio-button label="all">全部</el-radio-button>
              <el-radio-button label="collect">收藏</el-radio-button>
            </el-radio-group>          </div>
          <div style="width:90%" class="clearfix">
            <div
              ref="toggle_box"
              class="block"
              v-for="(item,index) in images"
              :key="index"
              @click="selected(item)"
              style="width: 170px; height: 170px;float:left;margin:15px 40px"
            >
              <div style="position: relative;">
                <el-image style="width: 170px; height: 170px" :src="item.url" fit="fit"></el-image>
                 <div style="width: 170px;
                  height: 170px;
                  position: absolute;
                  background: rgba(0, 0, 0, 0.5);
                  left: 0;
                  top: 0" v-if="item.url == select">
                  <img src="../../assets/selected.png" alt=""  style="margin:40px; width:100px;hight:100px">
                  </div>

              </div>
            </div>
          </div>
          <div>
            <el-pagination
            background
            :page='imagesQuery.page'
            :page-size='imagesQuery.per_page'
            layout="prev, pager, next"
            @current-change='currentPage'
            :total="total">
            </el-pagination>
          </div>
        </el-tab-pane>
        <el-tab-pane label="上传图片" name="imgsCtrl">
             <el-upload
                class="avatar-uploader"
                :headers="headers"
                action="http://ttapi.research.itcast.cn/mp/v1_0/user/images"
                name='image'
                :show-file-list="false"
                :on-success="handleAvatarSuccess"
                :before-upload="beforeAvatarUpload"
            >
                <img v-if="imageUrl" :src="imageUrl" class="avatar" />
                <i v-else class="el-icon-plus avatar-uploader-icon" style="line-height:178px"></i>
            </el-upload>
        </el-tab-pane>
      </el-tabs>
      <span slot="footer" class="dialog-footer">
        <el-button @click=" centerDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="submitImgs">确 定</el-button>
      </span>
    </el-dialog>
    <el-card>
      <div slot="header">
        <test v-if="$route.query.id" >修改文章</test>
        <test v-else ref="flag" >发布文章</test>
      </div>
      <el-form label-width="80px">
        <el-form-item label="标题:">
          <el-input v-model="publish_form.title" style="width:400px"></el-input>
        </el-form-item>
        <el-form-item label="内容:">
          <quill-editor v-model="publish_form.content" :options="editorOption"></quill-editor>
        </el-form-item>
        <el-form-item label="封面:">
          <el-radio-group v-model="publish_form.cover.type">
            <el-radio :label="1">单图</el-radio>
            <el-radio :label="2">三图</el-radio>
            <el-radio :label="-1">无图</el-radio>
            <el-radio :label="0">自动</el-radio>
          </el-radio-group>
          <div >
               <div v-if="publish_form.cover.type ==2 || publish_form.cover.type==1" class="addpic" style="float:left;margin-right:20px">
                    <img v-if="!publish_form.cover.images[0]" @click="selectRadio='imgs',pic_select=1,centerDialogVisible=true" src="../../assets/pic_bg.png" alt />
                    <img v-if="publish_form.cover.images[0]" @mouseenter="mouseenter=1" :src="publish_form.cover.images[0]" alt="">
                    <div v-if="mouseenter == 1"  @mouseleave="mouseenter=''" @click="publish_form.cover.images[0] = null,mouseenter=''" ><i class="el-icon-delete"></i></div>
                </div>
                <div v-if="publish_form.cover.type == 2" class="addpic"  style="float:left;margin-right:20px">
                    <img v-if="!publish_form.cover.images[1]" src="@/assets/pic_bg.png" @click="selectRadio='imgs',pic_select=2,centerDialogVisible=true" alt />
                    <img v-if="publish_form.cover.images[1]"  @mouseenter="mouseenter=2"  :src="publish_form.cover.images[1]" alt="">
                    <div v-if="mouseenter == 2"   @mouseleave="mouseenter=''" @click="publish_form.cover.images[1] = null,mouseenter=''" ><i class="el-icon-delete"></i></div>
                </div>
                <div v-if="publish_form.cover.type == 2" class="addpic"  style="float:left">
                    <img v-if="!publish_form.cover.images[2]" src="../../assets/pic_bg.png" @click="selectRadio='imgs',pic_select=3,centerDialogVisible=true" alt />
                    <img v-if="publish_form.cover.images[2]" @mouseenter="mouseenter=3" :src="publish_form.cover.images[2]" alt="">
                    <div v-if="mouseenter ==3"  @mouseleave="mouseenter=''" @click="publish_form.cover.images[2] = null,mouseenter=''"><i class="el-icon-delete"></i></div>
                </div>
          </div>
        </el-form-item >
        <el-form-item label='频道:'>
          <my-channel v-model="channel_content.channel_id"></my-channel>
        </el-form-item>
        <el-form-item v-if="$route.query.id">
          <el-button type="info" @click="edit">立即修改</el-button>
          <el-button @click="save">存入草稿</el-button>
        </el-form-item>
        <el-form-item v-else >
          <el-button type="primary" @click="submit">立即发布</el-button>
          <el-button @click="savePublish">存入草稿</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import local from '@/utils/local'
export default {
  data () {
    return {
      flag: true,
      publish_form: {
        title: '',
        content: '',
        cover: {
          type: 1,
          images: []
        },
        channel_id: ''
      },
      channel_content: {
        status: null,
        channel_id: null,
        begin_pubdate: null,
        end_pubdate: null,
        page: 2
      },
      publishName: '发布',
      mouseenter: '',
      pic_select: '',
      select: '',
      total: 0,
      imageUrl: '',
      headers: { Authorization: `Bearer ${local.getUser().token}` },
      imagesQuery: {
        page: 1,
        per_page: 10,
        collect: false
      },
      selectRadio: 'imgs',
      images: [],
      radio_all: 'all',
      centerDialogVisible: false,
      editorOption: {
        placeholder: '',
        modules: {
          toolbar: [
            ['bold', 'italic', 'underline', 'strike'],
            ['blockquote', 'code-block'],
            [{ header: 1 }, { header: 2 }],
            [{ list: 'ordered' }, { list: 'bullet' }],
            [{ indent: '-1' }, { indent: '+1' }],
            ['image']
          ]
        }
      }
    }
  },
  methods: {
    handleClick () {
      this.imageUrl = null
      this.select = null
    },
    async getImage () {
      const {
        data: { data }
      } = await this.$http.get('/user/images', { params: this.imagesQuery })
      this.images = data.results
      this.total = data.total_count
      this.imagesQuery.page = data.page
      this.imagesQuery.per_page = data.per_page
    },
    getToggle () {
      this.imagesQuery.collect = !this.imagesQuery.collect
      this.getImage()
    },
    currentPage (newPage) {
      this.imagesQuery.page = newPage
      this.getImage()
    },
    async collect (item) {
      await this.$http.put(`user/images/${item.id}`, {
        collect: !item.is_collected
      })
      this.getImage()
    },
    async publish (draft) {
      this.publish_form.channel_id = this.channel_content.channel_id
      await this.$http.post(`articles?draft=${draft}`, this.publish_form)
    },
    async editArt (draft) {
      this.publish_form.channel_id = this.channel_content.channel_id
      this.$http.put(`articles/${this.$route.query.id}?draft=${draft}`, this.publish_form)
    },
    async findArt (id) {
      const { data: { data } } = await this.$http(`articles/${id}`)
      this.publish_form = data
      this.channel_content.channel_id = this.publish_form.channel_id
    },
    handleAvatarSuccess (res, file) {
      this.imageUrl = res.data.url
      this.select = this.imageUrl
    },
    beforeAvatarUpload (file) {
      const isJPG = file.type === 'image/jpeg'
      const isLt2M = file.size / 1024 / 1024 < 2

      if (!isJPG) {
        this.$message.error('上传头像图片只能是 JPG 格式!')
      }
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 2MB!')
      }
      return isJPG && isLt2M
    },
    // 添加选中功能
    selected (item) {
      this.select = item.url
    },
    submitImgs () {
      switch (this.pic_select) {
        case 1 :
          this.publish_form.cover.images[0] = this.select
          break
        case 2 :
          this.publish_form.cover.images[1] = this.select
          break
        case 3 :
          this.publish_form.cover.images[2] = this.select
      }
      this.centerDialogVisible = false
    },
    submit () {
      this.publish(true)
    },
    savePublish () {
      this.publish(false)
    },
    edit () {
      this.editArt(true)
    },
    save () {
      this.editArt(false)
    }
  },
  created () {
    this.getImage()
  },
  mounted () {
    if (!this.$refs.flag) {
      this.findArt(this.$route.query.id)
    }
  },
  watch: {
    '$route.query' () {
      this.publish_form = {
        title: '',
        content: '',
        cover: {
          type: 1,
          images: []
        },
        channel_id: ''
      }
    }
  }
}
</script>

<style>
.addpic {
  position: relative;
  width: 150px;
  height: 150px;
  border: 1px dashed #dddddd;
}

.addpic img {
  position: absolute;
  width: 100%;
  height: 100%;
}
.addpic div {
  text-align: center;
  line-height: 150px;
  font-size: 50px;
  color: #fff;
  background: rgba(0, 0, 0, 0.5);
  position: absolute;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
}
</style>
